body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: floralwhite;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: darkblue;
}

.App-link {
  color: orange;
  text-decoration: none;
}

.App-content {
  padding: 2rem;
  font-size: calc(10px + 2vmin);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.button-group {
  margin: 0 auto;
  width: 20vw;
  font-size: 1.5rem;
  padding: 0.5em;
  margin-bottom: 1em; }
  .button-group .value {
    display: inline-block;
    width: 40%; }
  .button-group .button {
    display: inline-block;
    height: 2.5em;
    border: 1px solid burlywood;
    background: transparent;
    border-radius: 8px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer; }
  .button-group .button:focus {
    outline: none;
    border-color: orange;
    box-shadow: 0 0 5px orange; }
  .button-group .button--left {
    width: 30%; }
  .button-group .button--right {
    width: 30%; }
  .button-group .button--full-width {
    width: 100%; }

.my-form {
  font-size: 1rem;
  padding: 5px;
  width: 20rem;
  margin: 0 auto;
  margin-bottom: 1em; }
  .my-form .field-group {
    margin-bottom: 10px; }
  .my-form .field-label {
    font-weight: bold;
    text-transform: capitalize;
    margin-right: 5px;
    width: 30%;
    text-align: right;
    display: inline-block; }
  .my-form .field-text {
    height: 2.5em;
    width: 10vw;
    border-radius: 5px;
    padding: 0 10px;
    border: 1px solid burlywood; }
    .my-form .field-text:focus {
      border-radius: 5px;
      box-shadow: 0 0 5px orange;
      border-color: orange;
      outline: none; }
  .my-form .field-radio-group {
    display: inline-block; }
  .my-form .field-radio {
    color: yellow; }
  .my-form .btnReset {
    display: inline-block;
    height: 2.5em;
    border: 1px solid burlywood;
    background: transparent;
    border-radius: 8px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    width: 100%; }
    .my-form .btnReset:focus {
      outline: none;
      border-color: orange;
      box-shadow: 0 0 5px orange; }

.callback-count-container {
